
.result {
  height: 100%;
}
.result .CodeMirror {
  box-sizing: border-box;
  height: 100%;
  border: 2px solid #fff;
  transition: border ease 0.15s;
  background-color: #fbfbfb;
}
.result .CodeMirror-focused {
  border-color: #ff3304;
}


