
body {
  font-family: Helvetica, sans-serif;
  margin: 0;
}
#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
}
.section {
  -ms-flex: 1;
      flex: 1;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.label {
  position: absolute;
  top: 4px;
  right: 4px;
  z-index: 10;
}
.label span {
  display: inline-block;
  font-size: 12px;
  color: #ccc;
  margin-right: 5px;
}
.copyright {
  border-right: 2px solid transparent;
  border-bottom: 2px solid transparent;
  background-clip: padding-box;
  z-index: 10;
}


